import React from 'react';
import style from './report.module.css';
import axios from 'axios';
import {withRouter} from 'react-router-dom';


const Report = (props) =>{
  
    const showReportHandler=(ele)=>{
 
// console.log(ele.id);
        axios({
            url:'/api/patient/report/'+ele.id,
            method:'GET',
            headers:{
                Authorization: 'Bearer '+localStorage.getItem('ptoken')
            }
        }).then(Response=>{

            // console.log(Response.data)


            props.history.push({
                pathname: '/patient/report/'+Response.data.id,
                state: { report: Response.data,
                        who:'patient',
                        which:'ptoken'
                }

              })



            // <Redirect
            // to={{
            //     pathname: "/patient/report/'+ele.id",
            //     state: { report: Response.data }
            // }}
            // />
    })}

// console.log(props.report);


   const element=(
        <div className={style.mainCard} >
            {props.report.map((ele,i)=>{
    return <div className={style.card} key={i}>
            <p>Id : {ele.id}</p>
            <p>Diseases : {ele.dieseases}</p>
            <p>Doctor ID : {ele.doctorId}</p>

            <p className={style.showBtn} onClick={()=>showReportHandler(ele)} >Show</p>
            
            {/* <Link
                to={{
                    pathname: "/patient/report/"+ele.id,
                    state: { reportId:ele.id }
                }}
                 className={style.showBtn}
            >Show</Link> */}


        </div>
            })}
        </div>
    )



return(
    
    <React.Fragment>

            <div>
            <h1>Patient Reports</h1>
            
                {element}
                {/* {selectedReport?
                    <FullReport
                         elements={selectedReport}
                    />  :null
                }
                    */}
            
            </div>
           
    </React.Fragment>
)
}

export default withRouter(Report);