import React, { Component } from 'react';
// import { useForm } from 'react-hook-form'
import axios from 'axios';

import Navbar from '../../navbar/navbar';
import styles from '../../../assest/form.module.css';
import Spinner from '../../Spinner/spinner';


class UploadReport extends Component{


    state={
        mulfiles:null,

        email:null,
        comments:null,
         precautions:null,
        dieseases:null,
        city:null,
        loading:false,

    }


    handleFile(e){
        console.log(e.target.files[0])
        this.setState({mulfiles:e.target.files[0]})
        console.log(this.state.mulfiles)

    }


    handleUpload=(e)=>{
  
        // this.setState({loading:true});
        console.log(this.state);
        // let file = this.state.mulfiles;

        let formdata = new FormData();

        // formdata.append('patientId',this.state.patientId);
        formdata.append('email',this.state.email);
        formdata.append('dieseases',this.state.dieseases);
        formdata.append('comments',this.state.comments);
        formdata.append('medicines',this.state.medicines);
        
        formdata.append('precautions',this.state.precautions);
        formdata.append('city',this.state.city);
        
        formdata.append('mulfiles',this.state.mulfiles);
        // console.log(formdata);
        axios({
            url:'/api/doctor/report',
            method:'POST',
            headers:{
                Authorization: 'Bearer '+localStorage.getItem('dtoken')
            },
            data:formdata
 
        }).then(Response=>{
        this.setState({loading:false});
           alert('file uploaded successfully'); 
                document.getElementById("f").reset();

            
        }).catch(error =>{
        this.setState({loading:false});
        console.log(error);

        })
    }

   

   
     render(){

        return(

        <React.Fragment>
            <Navbar/>

            {this.state.loading?<Spinner/>:null}  
            <div className={styles.body}>
                      

            <h1>Upload Report Here</h1>      

            <div className={styles.wrap}>

            <form className={styles.input} id='f'>
                

                {/* <input type='text' name='patientId' placeholder='Patient Id' className={styles.input100} required  onChange={ e => this.setState({ patientId : e.target.value })} /> */}
                <input type='email' name='email' placeholder='Patient Email ' className={styles.input100} required  onChange={ e => this.setState({ email : e.target.value })} />
      {/* {<span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>} */}
               
                {/* <input type='text' name='hospitalId' placeholder='Hospital Id' className={styles.input100} required onChange={ e => this.setState({ hospitalId : e.target.value })} /> */}
                <input type='text' name='dieseases' placeholder='Dieseases' className={styles.input100} required onChange={ e => this.setState({ dieseases : e.target.value })} />
                
                <input type='text' name='comments' placeholder='Comments' className={styles.input100} required onChange={ e => this.setState({ comments : e.target.value })} />
                <input type='text' name='medicines' placeholder='Medicines' className={styles.input100} required onChange={ e => this.setState({ medicines : e.target.value })} />
                <input type='text' name='precautions' placeholder=' precautions' className={styles.input100} required onChange={ e => this.setState({  precautions : e.target.value })} />
                <input type='text' name='city' placeholder='city' className={styles.input100} required onChange={ e => this.setState({ city : e.target.value.toLowerCase().trim() })} />

               

                <input type='file' name='file' onChange={e=>this.handleFile(e) } />
                {/* {console.log(this.state)} */}
                {/* this.setState({ mulfiles: e.target.files }) */}
                
                <button type="button" onClick={(e)=>this.handleUpload(e)} className={styles.btn}>SUBMIT</button>
            </form>
            </div>
           </div>
        </React.Fragment>
    )
}
}

export default UploadReport;