import React,{  Component } from 'react';

import axios from 'axios';
import Report from '../../Report/report'
import Navbar from '../../navbar/navbar';

class PatientReport extends Component{
    state={
        response:null
        
    }
   componentDidMount(){
    localStorage.getItem('ptoken');
    axios({
        url:'/api/patient/report',
        method:'GET',
        headers:{
            Authorization: 'Bearer '+localStorage.getItem('ptoken')
        }
    }).then(Response=>{
       
//  console.log(Response)
        this.setState({
            response:Response.data
        })

   
    })

    }

   
       

    render(){
       
        if(this.state.response!=null){
            return(
                <React.Fragment>
                    <Navbar/>
                     <div>
                         <Report 
                             report={this.state.response}
                    /> 
                       
                    </div>
                </React.Fragment>
            )
        }
        return(
            <React.Fragment>
                    <Navbar/>

                <h1>Nothing to show</h1>
            </React.Fragment>
        )
    }
}

export default PatientReport;