import React from 'react';
import style from './report.module.css';
import Navbar from '../navbar/navbar';
import axios from 'axios'


const HReport = (props) =>{

    // let [selectedReport, setSelectedReport] = useState(null);

    const showReportHandler=(ele)=>{
        //    setSelectedReport(ele);
           axios({
            url:'/api/hospital/report/'+ele.id,
            method:'GET',
            headers:{
                Authorization: 'Bearer '+localStorage.getItem('htoken')
            }
        }).then(Response=>{

            // console.log(Response.data)


            props.history.push({
                pathname: '/hospital/report/'+Response.data.id,
                state: { report: Response.data,
                        who:'hospital',
                        which:'htoken'
                }

              })
    })
    }



   const element=(
    

        <div className={style.mainCard} >
            {props.location.state.report.map((ele,i)=>{
    return <div className={style.card} key={i}>
            <p>Id : {ele.id}</p>
            <p>Diseases : {ele.dieseases}</p>
            <p>Doctor ID : {ele.doctorId}</p>


            <p className={style.showBtn} onClick={()=>showReportHandler(ele)} >Show</p>
        </div>
            })}
        </div>
    )



return(
    
    <React.Fragment>
            <Navbar />

            
            <h1>Patient Reports</h1>
                {element}


    </React.Fragment>
)
}

export default HReport;