import React,{useState} from 'react';
import { useForm } from 'react-hook-form'
import axios from 'axios';

import Navbar from '../../navbar/navbar';
import styles from '../../../assest/form.module.css';
import style from './DoctorInterface.module.css';
import Spinner from '../../Spinner/spinner'

const DoctorInterface =(props)=>{

    const { register,handleSubmit, errors } = useForm();
    let [loading, setloading] = useState(false);
    let [error, seterror] = useState(false);


    const onSubmit = data => { 
        localStorage.setItem('pemail', JSON.stringify(data));
        // console.log(data)
        setloading(true);
        axios({
            url:'/api/doctor/report/index',
            method:'POST',
            headers:{
                Authorization: 'Bearer '+localStorage.getItem('dtoken')
            },
            data:data
 
        }).then(Response=>{
            // console.log(Response.data)
            if(Response.status===200){
                props.history.push('/doctor/report', {
                    report: Response.data
                  })
            }
                seterror(false)
                setloading(false);                            
            }).catch(error=>{
                seterror(true);
                // seterrorcode(error.response.data.message);
                setloading(false);

            })
     }



    const addHandler=()=>{
        props.history.push({
            pathname:'/doctor/upload-report',
        });
    }




    return(
        <React.Fragment>
            <Navbar/> 
            {loading ? <Spinner/> : null}            

        <h1>Doctor Interface</h1>
        <div className={style.splitter}>

       
        <div>
        <form onSubmit={handleSubmit(onSubmit)} className={styles.input}>
        <input type="email" name="email" placeholder="Enter an E-mail" className={styles.input100}  ref={register({ required: true })} />
      {errors.email && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}
      {error ?<span className={styles.errors}>No content found <i className="fa fa-exclamation-circle"></i> </span>:null}
      <button type='submit' className={styles.btn}  >SEARCH REPORT</button>
      </form>
        
        </div>
        <hr style={{margin:0}}></hr>
        <div>
        <input type="email" placeholder="Want to add Report ?" className={styles.input100} style={{backgroundColor:"#f2f2f2"}} disabled/>
        <button type='button' className={styles.btn} onClick={addHandler}>ADD REPORT</button>
        </div>
        </div>
        </React.Fragment>
    )
}

export default DoctorInterface;