import React,{useState} from 'react';
import {withRouter } from 'react-router-dom';
import axios from 'axios';
import Navbar from '../navbar/navbar';
import Spinner from '../Spinner/spinner'; 
import style from './FullReport.module.css';
import styles from '../../assest/form.module.css';
import fileDownload from 'js-file-download';


const FullReport = (props) =>{


    
    let [loading,setLoading] = useState(false);
    // let [dataL,setData] = useState();



// useEffect(()=>{
//     setData(props.history.location.state.report);
// },[props])




    
// const closeHandler=()=>{
    // console.log(showReport);
    // setShowReport(!showReport);
// }





    const deleteHandler=()=>{
        setLoading(true);
        axios({
            url:'/api/doctor/report/'+props.history.location.state.report.id,
            method:'DELETE',
            headers:{
                Authorization: 'Bearer '+localStorage.getItem('dtoken')
            }
        }).then(Response=>{
        setLoading(false);

        axios({
            url:'/api/doctor/report/index',
            method:'POST',
            headers:{
                Authorization: 'Bearer '+localStorage.getItem('dtoken')
            },
            data:JSON.parse(localStorage.getItem('pemail'))
 
        }).then(Response=>{
            // console.log(Response.data)
            if(Response.status===200){
                props.history.push('/doctor/report', {
                    report: Response.data
                  })
            }
                          
            }).catch(error=>{

console.log(error)
            })


            alert('Report deleted successfully'); 


            // props.history.goBack();

        }).catch(error =>{
        setLoading(false);
            console.log(error)
        })
    }


  
    const updateHandler=()=>{
        
        document.querySelectorAll("input").readOnly="false";

        props.history.push('/doctor/update-report', {
            elements: props.history.location.state.report
          })

    }


    const downloadHandler=()=>{

        setLoading(true);

        axios({
            url:'/api/'+props.history.location.state.who+'/report/'+props.history.location.state.report.id+'/'+props.history.location.state.report.files[0].name,
            method:'GET',
            headers:{
                Authorization: 'Bearer '+localStorage.getItem(props.history.location.state.which)
            },
            responseType:'blob'
        }).then(Response=>{
            fileDownload(Response.data, props.history.location.state.report.files[0].name)


            setLoading(false);
            // console.log(Response);

        }).catch(error =>{
        setLoading(false);
            console.log(error)
        })

    }



return(
    
    <React.Fragment>
        <Navbar/>
        {loading ? <Spinner/> : null} 

    <div className={style.full}>

<h1> patients Report</h1>
    <form className={style.input}>
        

     
        <label>Id :</label>   <input type='text' name='Id' className={styles.input100} value={props.history.location.state.report.id} disabled/>
        <label>Patient Id :</label>   <input type='text' name='patientId' className={styles.input100} value={props.history.location.state.report.patientId} disabled/>
        <label>Hospital Id :</label>   <input type='text' name='hospitalId' className={styles.input100} value={props.history.location.state.report.hospitalId} disabled/>
        <label>Diesease Name :</label>   <input type='text' name='dieseases' className={styles.input100} value={props.history.location.state.report.dieseases} disabled/>
        <label>Comments :</label>   <input type='text' name='comments'  className={styles.input100} value={props.history.location.state.report.comments} disabled/>
        <label>Medicines :</label>   <input type='text' name='medicines'  className={styles.input100} value={props.history.location.state.report.medicines} disabled/>
        <label>Precautions :</label>   <input type='text' name='precautions' className={styles.input100} value={props.history.location.state.report.precautions} disabled/>
        <label>Created At :</label>   <input type='text' name='createdAt' className={styles.input100} value={props.history.location.state.report.createdAt} disabled/>
        <label>Updated At :</label>   <input type='text' name='updatedAt' className={styles.input100} value={props.history.location.state.report.updatedAt} disabled/>
       
       {props.history.location.state.report.files[0]!==undefined ? <p className={style.download} onClick={downloadHandler}>click here to download attachment</p>:null}

<div className={style.buttons}>


{localStorage.getItem("dtoken")? <p className={style.Btn} onClick={updateHandler}>UPDATE</p>:null}
   {localStorage.getItem("dtoken")? <p className={style.Btn} onClick={deleteHandler}>DELETE</p>:null}
  
    <p className={style.Btn} onClick={props.history.goBack}>CLOSE</p>
</div>
   



    </form>

 </div>

            
        

            
            
           
    </React.Fragment>
)
}

export default withRouter(FullReport);