import React from 'react';
import {withRouter} from 'react-router-dom';

import styles from './NotFound.module.css'
import logo from './notFound.png';
import Navbar from '../navbar/navbar';

const NotFound=(props)=>{
    return(
        <React.Fragment>
            <Navbar/>
            <div>
                <div className={styles.container}>
           
                       <img className={styles.image} src={logo} alt={'Error 404'} />
            
                        <p id='info'>
                            <span>Oops...!</span><br></br>We can't seem to find <br></br> the page you're looking for.
                            <button className={styles.btn} onClick={props.history.goBack}>Back</button>
                        
                        </p>
             
                </div>
                
            </div>
        </React.Fragment>
    )
}

export default withRouter(NotFound);