import React ,{useState}from 'react';
import { useForm } from 'react-hook-form'
import axios from 'axios';
import {Link } from 'react-router-dom';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

import Navbar from '../../navbar/navbar';
import styles from "../../../assest/form.module.css";
import Spinner from '../../Spinner/spinner';

const PatientSignup =(props)=>{

    const { register, handleSubmit, errors } = useForm();
    let [loading, setloading] = useState(false);
    const [selectedDate, setSelectedDate] = useState(null);


    const onSubmit = data => { 
        setloading(true);
        axios({
            url:'/api/patient/signup',
            method:'PUT',
            headers:{
                'Content-Type':'application/json'
            },
            data:JSON.stringify({
                username:data.username,
                password:data.password,
                name:data.name,
                gender:data.gender,
                date:selectedDate,
                address:data.address,
                mobile:data.mobile,
                email:data.email
            })
        }).then(Response=>{
            setloading(false);
            props.history.push('/patient/login')               
        }).catch(error =>{
        setloading(false);

        }
        )
     }

    return(
        <React.Fragment>
            <Navbar/>
            {loading?<Spinner/>:null}            

            <h1>Patient Signup Form</h1>

        <div className={styles.wrap}>

        <form onSubmit={handleSubmit(onSubmit)} className={styles.input}>
   
      <input type='text' name="username" className={styles.input100} placeholder='User Name'  ref={register({ required: true })} />
      {errors.username && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}

      <input type='password' minLength="8" name="password" className={styles.input100} placeholder='Password' ref={register({ required: true })} />
      {errors.password && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}

      <input type='email' name="email" className={styles.input100} placeholder='E-mail' ref={register({ required: true })} />
      {errors.email && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}

      <input type='text' name="name"className={styles.input100}  placeholder='Name' ref={register({ required: true })} />
      {errors.name && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}

      <input type='text' name="gender" className={styles.input100} placeholder='Gender' ref={register({ required: true })} />
      {errors.gender && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}

      {/* <input type='date' name="date" className={styles.input100Date}  ref={register({ required: true })} /> */}
      {/* {errors.date && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>} */}


      <DatePicker
        selected={selectedDate}
        onChange={date=>setSelectedDate(date)}
        dateFormat='dd-MM-yyyy'
        maxDate={new Date()}
        showYearDropdown
        scrollableYearDropdown
        required
        isClearable
        placeholderText="Birthdate"
        className={styles.input100}
        // style={{margin:'0 !important'}}
      />



      <input type='text' name="address" className={styles.input100} placeholder='Address' ref={register({ required: true })} />
      {errors.address && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}

      <input type='tel' name="mobile" className={styles.input100} placeholder='Mobile' ref={register({ required: true })} />
      {errors.mobile && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}
        
      
      
      <button type="submit" className={styles.btn}>SUBMIT</button>
                    
        <p>Already have an account?<Link to='/patient/login'> Login</Link> </p>
        </form>
        
       

        </div>

        </React.Fragment>
    )
}

export default PatientSignup;