import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import axios from 'axios';
import {Link} from 'react-router-dom';

import Navbar from '../../navbar/navbar';

import styles from '../../../assest/form.module.css';
import Spinner from '../../Spinner/spinner';

const HospitalLogin =(props)=>{
    const { register, handleSubmit, errors } = useForm();
    
    let [error, seterror] = useState(false);
    let [errormsg,seterrormsg]=useState("something went wrong")
    let [errorcode, seterrorcode] = useState('');
    let [isEmptyU, setisEmptyU] = useState(true);
    let [isEmptyP, setisEmptyP] = useState(true);
    let [loading, setloading] = useState(false);
    

    useEffect(()=>{
        if(errorcode==="1"){
            // console.log("Username doesn't exist ")
            seterrormsg("A user with this username could not be found")
            
        }else if(errorcode==="2"){
            // console.log("password not matched")
            seterrormsg("Wrong password")
        
        }

       
    },[error,errorcode,errormsg])

    
    const onSubmit = data => { 
    setloading(true); 
        axios.post('/api/hospital/login',data)
            .then(Response=>{

                localStorage.setItem('htoken',Response.data.token);
                localStorage.setItem('username',data.username);
                setloading(false); 
                props.history.push('/hospital/interface')               

                    
            }).catch(error=>{
                seterror(true);
                // seterrorcode(error.response.data.message);
                setloading(false);
            })
     }

    
       
        return(
            
            <React.Fragment>
            <Navbar />   
            {loading?<Spinner/>:null}
            <h1>Hospital Login Form</h1>
    
        <div className={styles.wrap}>
    
            <form onSubmit={handleSubmit(onSubmit)}  className={styles.input}>
       
            <input name="username" placeholder="username" className={styles.input100} onChange={ e => {if(e.target.value.trim()===''){setisEmptyU(true);  seterrorcode('')}else{setisEmptyU(false)}}} ref={register({ required: true })} />
      {errors.username && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}
      {errorcode==='1' && !isEmptyU ?<span className={styles.errors}>username not found <i className="fa fa-exclamation-circle"></i> </span>:null}
      
      <input name="password" type='password' placeholder="password" className={styles.input100} onChange={ e => {if(e.target.value.trim()===''){setisEmptyP(true);  seterrorcode('')}else{setisEmptyP(false)}}}  ref={register({ required: true })} />
      {errors.password && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}
      {errorcode==='2' && !isEmptyP ? <span className={styles.errors}>password you entered do not match <i className="fa fa-exclamation-circle"></i> </span>:null}
        
      
      <button type='submit' className={styles.btn} onClick={()=>seterror(false)} >SUBMIT</button>
          <p>Don't have an account?<Link to='/hospital/signup'> Sign up</Link> </p>

            </form>
    
            {/* <Route exact path='/hospital/interface' component={HospitalInterface}/> */}
            
    
            </div>
            </React.Fragment>
        )
  
    
}

export default HospitalLogin;