import styles from './navbar.module.css';
import React from 'react';
import {NavLink,withRouter} from 'react-router-dom';

const Navbar = (props) => {



const logoutHandler=()=>{
    localStorage.clear();
    // return <Redirect to='/'  />
    props.history.push("/");
    
}

const redirectHome=()=>{
    props.history.push("/");
    
}


    return(
        // <header>
            <div className={styles.header}>
                    <div  className={styles.navv}>
                        <nav>
                            <ul className={styles.nav__links}>
                            <li><NavLink className={styles.nava} to='/patient/login'>Patient</NavLink></li>
                            <li><NavLink className={styles.nava} to='/hospital/login'>Hospital</NavLink></li>
                            <li><NavLink className={styles.nava} to='/doctor/login'>Doctor</NavLink></li>
                            </ul>
                        </nav>
                </div>
                <div className={styles.loglogo}>
                    {localStorage.getItem('username') ? 
                            <div className={styles.loginfo}>
                                   Loged in as <span className={styles.spanN}>{localStorage.getItem('username')}</span> 
                                   <div className={styles.containerLogBtn}>
                                   <p className={styles.logbtn} onClick={logoutHandler}>Logout</p>
                                   </div>
                                   
                        </div> : null }
                    {/* <div className={styles.loginfo}>Loged in as {props.name}</div> */}
                    <div className={styles.logo}  onClick={redirectHome}></div>
                </div>
            </div>
        // </header>

    )
    }

export default withRouter(Navbar);

