import React,{useState} from 'react';
import { withRouter } from 'react-router-dom';
import styles from '../../../assest/form.module.css';
import Navbar from '../../navbar/navbar';
import axios from 'axios';
import Spinner from '../../Spinner/spinner';

const UpdateReport = (props) =>{



    let [mulfiles] = useState(props.location.state.elements.files[0]);
    let [patientId] = useState(props.location.state.elements.patientId);
    let [hospitalId] = useState(props.location.state.elements.hospitalId);
    let [comments,setComments] = useState(props.location.state.elements.comments);
    let [medicines,setMedicines] = useState(props.location.state.elements.medicines);
    let [precautions,setPrecautions] = useState(props.location.state.elements.precautions);
    let [dieseases] = useState(props.location.state.elements.dieseases);
    let [city,setCity] = useState(props.location.state.elements.city);
    let [loading,setLoading] = useState(false);

 

        const saveHandler=()=>{
            // console.log(props);
  
            setLoading(true);
           
            let file = mulfiles;
    
            let formdata = new FormData();
    
            formdata.append('patientId',patientId);
            formdata.append('hospitalId',hospitalId);
            formdata.append('dieseases',dieseases);
            formdata.append('comments',comments);
            formdata.append('medicines',medicines);
            
            formdata.append('precautions',precautions);
            formdata.append('city',city);
            formdata.append('mulfiles',file);
            // console.log(formdata);
            axios({
                url:'/api/doctor/report/'+props.location.state.elements.id,
                method:'PUT',
                headers:{
                    Authorization: 'Bearer '+localStorage.getItem('dtoken')
                },
                data:formdata
     
            }).then(Response=>{
            setLoading(false);
               alert('Report updated successfully'); 
               

                props.history.push({
                    pathname: '/doctor/report/'+Response.data.id,
                    state: { report: Response.data,
                            who:'doctor',
                            which:'dtoken'
                    }
    
                  })



               
            }).catch(error =>{
            setLoading(false);
                console.log(error)
            })
        }


return(
    
    <React.Fragment>

            <Navbar />
            {loading?<Spinner/>:null}
            <div className={styles.wrap}>
                
            <h1>Edit Report</h1>      
            

            <form className={styles.input}>


                <div className={styles.contain}><label>Id :</label>   <input type='text' name='Id' className={styles.input100}   value={props.location.state.elements.id} disabled/> </div>
                <div className={styles.contain}><label>Patient Id :</label>   <input type='text' name='patientId' className={styles.input100}   value={props.location.state.elements.patientId} disabled/> </div>
                <div className={styles.contain}><label>Hospital Id :</label>   <input type='text' name='hospitalId' className={styles.input100}   value={props.location.state.elements.hospitalId} disabled/> </div>
                <div className={styles.contain}><label>Diesease Name :</label>   <input type='text' name='dieseases' className={styles.input100}   value={props.location.state.elements.dieseases} disabled/> </div>
                <div className={styles.contain}><label>Medicines :</label> <input type='text' name='medicines' className={styles.input100} defaultValue={props.location.state.elements.medicines} onChange={ e => setMedicines(e.target.value) }/> </div>
                <div className={styles.contain}><label>Comments :</label>   <input type='text' name='comments' className={styles.input100}    defaultValue={props.location.state.elements.comments} onChange={ e => setComments(e.target.value) }/> </div>
                <div className={styles.contain}><label>Precautions :</label>   <input type='text' name='precautions' className={styles.input100}   defaultValue={props.location.state.elements.precautions} onChange={ e => setPrecautions(e.target.value) }/> </div>
                <div className={styles.contain}><label>city :</label>   <input type='text' name='city' className={styles.input100}   defaultValue={props.location.state.elements.city} onChange={ e => setCity(e.target.value) } disabled/> </div>

                <p className={styles.btn} onClick={saveHandler} style={{width:200}}>Save Changes</p>
            <p className={styles.btn}  onClick={props.history.goBack} style={{width:200}}>Close</p>



              
            </form>
 
         </div>
            
            
           
    </React.Fragment>
)
}

export default withRouter(UpdateReport);