import React from 'react';
import style from './report.module.css';
import axios from 'axios'
import Navbar from '../navbar/navbar';

const DReport = (props) =>{

    // console.log(props);

    const showReportHandler=(ele)=>{
      
        axios({
            url:'/api/doctor/report/'+ele.id,
            method:'GET',
            headers:{
                Authorization: 'Bearer '+localStorage.getItem('dtoken')
            }
        }).then(Response=>{

            // console.log(Response.data)


            props.history.push({
                pathname: '/doctor/report/'+Response.data.id,
                state: { report: Response.data,
                        who:'doctor',
                        which:'dtoken'
                }

              })
    })
}


   const element=(
    

        <div className={style.mainCard} >
            {props.location.state.report.map((ele,i)=>{
    return <div className={style.card} key={i}>
            <p>Id : {ele.id}</p>
            <p>Diseases : {ele.dieseases}</p>
            <p>Doctor ID : {ele.doctorId}</p>


            <p className={style.showBtn} onClick={()=>showReportHandler(ele)} >Show</p>
        </div>
            })}
        </div>
    )



return(
    
    <React.Fragment>
            <Navbar />

            
            <h1>Patient Reports</h1>
                {element}

    </React.Fragment>
)
}

export default DReport;