import React from 'react';
import {Pie} from 'react-chartjs-2';

const PieChart=(props)=>{



return(
    <React.Fragment>
        <Pie 
            data={props.info} 
            
            options={{
                title:{
                  display:true,
                  text:props.text,
                  fontSize:20
                }
            }}
            />
    </React.Fragment>
)
}

export default PieChart;