import React,{useState} from 'react';
import { useForm } from 'react-hook-form'
import axios from 'axios';
import {withRouter} from 'react-router-dom';
import Navbar from '../../navbar/navbar'
import styles from '../../../assest/form.module.css';
// import style from './DoctorInterface.module.css';
import Spinner from '../../Spinner/spinner';
import style1 from './hinterface.module.css'


const HospitalInterface =(props)=>{
    const { register,handleSubmit, errors } = useForm();
    let [loading, setloading] = useState(false);
    let [error, seterror] = useState(false);
    let [email, setEmail] = useState(null);


    const onSubmit = data => { 
        console.log(email);
        console.log(data);

        setloading(true);
        // axios.get('/api/doctor/report',data)
        axios({
            url:'/api/hospital/report/index',
            method:'POST',
            headers:{
                Authorization: 'Bearer '+localStorage.getItem('htoken')
            },
            data:data
 
        }).then(Response=>{
                props.history.push('/hospital/report', {
                    report: Response.data
                  })
                console.log(Response.data);
                setloading(false);                            
            }).catch(error=>{
                seterror(true);
                // seterrorcode(error.response.data.message);
                setloading(false);

            })
     }




    return(
        <React.Fragment>
            {loading ? <Spinner/> : null}            

            <Navbar/> 
        <h1>Hospital Interface</h1>
        <div className={style1.splitter}>

       
        <div>
        <form onSubmit={handleSubmit(onSubmit)} className={styles.input}>
        <input type="email" name="email" placeholder="Enter an E-mail" className={styles.input100}  onChange={ e => setEmail(e.target.value)} ref={register({ required: true })} />
      {errors.email && <span className={styles.errors}>This field is required <i className="fa fa-exclamation-circle"></i> </span>}
      {error ?<span className={styles.errors}>No content found <i className="fa fa-exclamation-circle"></i> </span>:null}
      <button type='submit' className={styles.btn} >SEARCH REPORT</button>
      </form>
        
        </div>
        <hr style={{margin:0}}></hr>
        <div>
        <input type="email" placeholder="Want to add new Doctor ?" className={styles.input100} style={{backgroundColor:"#f2f2f2"}} disabled/>
        <button type='button' className={styles.btn} onClick={()=>{props.history.push({pathname:'/doctor/signup'})}}>ADD DOCTOR</button>

        </div>
        </div>

        </React.Fragment>
    )
}

export default withRouter(HospitalInterface);