import React, { useState, useEffect } from "react";
import Navbar from '../../navbar/navbar'
import style from './Analytics.module.css'

import Select from 'react-select';
import PieChart from './pieChart'
// import { Pie } from "react-chartjs-2";
import axios from "axios";

const Analytics = () => {


  const [chartData, setChartData] = useState({});
  const [chartData1, setChartData1] = useState({});

  const [selectedCity, setSelectedCity] = useState();
  const [selectedDie, setSelectedDie] = useState();


  const handleChange = data => { 
setSelectedCity(data.value)
 }
 const handleChange1 = data => { 
setSelectedDie(data.value)
 }

 const options = [
    { value: 'kolhapur', label: 'kolhapur' },
    { value: 'satara', label: 'satara' },
    { value: 'sangali', label: 'sangali' },
    { value: 'karad', label: 'karad' },
    { value: 'pune', label: 'pune' },
    { value: 'mumbai', label: 'mumbai' },
    { value: 'solapur', label: 'solapur' },
    { value: 'thane', label: 'thane' },
  ];
  const options1 = [
    { value: 'corona', label: 'corona' },
    { value: 'fever', label: 'fever' },
    { value: 'cold', label: 'cold' },
    { value: 'cough', label: 'cough' },
    { value: 'maleria', label: 'maleria' },
    { value: 'diabetes', label: 'diabetes' },
  ];



  const chart = () => {
    let die = [];
    let count = [];

    axios({
        url:'/api/admin/analytics/city/'+selectedCity,
        method:'GET',
        headers:{
            Authorization: 'Bearer '+localStorage.getItem('atoken')
        }
    }).then(res => {

            for (const dataObj of res.data.diseases) {
                die.push(dataObj);
              }
            for (const dataObj of res.data.count) {
                count.push(dataObj);
              }
            setChartData1({
              labels: die,
              datasets: [
                {
                  data: count,
                  backgroundColor: ['#003f5c',
                    '#2f4b7c',
                    '#665191',
                    '#a05195',
                    '#d45087',
                    '#f95d6a',
                    '#ff7c43',
                    '#ffa600'],
                  borderWidth: 4
                }
              ]
            });


      })
      .catch(err => {
        console.log(err);
      });

  };


  const chart1 = () => {
    let city1 = [];
    let count1 = [];

    axios({
        url:'/api/admin/analytics/disease/'+selectedDie,
        method:'GET',
        headers:{
            Authorization: 'Bearer '+localStorage.getItem('atoken')
        }
    }).then(res => {

        for (const dataObj of res.data.city) {
            city1.push(dataObj);
          }
        for (const dataObj of res.data.count) {
            count1.push(dataObj);
          }
        setChartData({
          labels: city1,
          datasets: [
            {
              data: count1,
              backgroundColor: ['#003f5c',
              '#2f4b7c',
              '#665191',
              '#a05195',
              '#d45087',
              '#f95d6a'],
              borderWidth: 4
            }
          ]
        });

      })
      .catch(err => {
        console.log(err);
      });

  };

  useEffect(() => {
    chart();
    chart1();
  },[selectedCity,selectedDie]);



  return (
    <React.Fragment>
        <Navbar/>
      <h1>Regional Analasis</h1>
      <div className={style.mainContainer}>
      <div className={style.smallContainer}>

<div>
      <Select className={style.tag}
        value={selectedCity}
        onChange={handleChange}
        options={options}
        placeholder={selectedCity}
        defaultInputValue="Select the city"
      />
      </div>
      <div>
      {selectedCity?
        <PieChart
        info={chartData1}
        text={"As per "+selectedCity}
        />:null}
</div>

      </div>

      <div className={style.smallContainer}>

<div>
<Select className={style.tag}
  value={selectedDie}
  onChange={handleChange1}
  options={options1}
  placeholder={selectedDie}
  defaultInputValue="Select the Diesease"
/>
</div>
<div>
{selectedDie?
  <PieChart
  info={chartData}
  text={"As per "+selectedDie}
  />:null}
</div>

</div>
</div>
      </React.Fragment>
  );
};

export default Analytics;