import React, { Component } from 'react';
import {Switch, Route, withRouter} from 'react-router-dom';

import AdminLogin from '../../components/Admin/AdminLogin/AdminLogin';
import Analytics from '../../components/Admin/Analytics/Analytics';

import Home from './Home';

import PatientLogin from '../../components/Patient/PatientLogin/PatientLogin';
import PatientSignup from '../../components/Patient/PatientSignup/PatientSignup';
import HospitalLogin from '../../components/Hospital/HospitalLogin/HospitalLogin';
import HospitalSignup from '../../components/Hospital/HospitalSignup/HospitalSignup';
import DoctorLogin from '../../components/Doctor/DoctorLogin/DoctorLogin';
import DoctorSignup from '../../components/Doctor/DoctorSignup/DoctorSignup';

import PatientReport from '../../components/Patient/PatientReport/PatientReport';
import DReport from '../../components/Report/DReport'
import HReport from '../../components/Report/HReport'
import UploadReport from '../../components/Doctor/UploadReport/UploadReport';
import UpdateReport from '../../components/Doctor/UpdateReport/UpdateReport';
import HospitalInterface from '../../components/Hospital/HospitalInterface/HospitalInterface';

import Logout from '../../components/logout/logout';
import Notfound from '../../components/NotFound/NotFound';
import DoctorInterface from '../../components/Doctor/DoctorInterface/DoctorInterface';

import FullReport from '../../components/Report/FullReport';

class Main extends Component{


    render(){
         
        return(
            <React.Fragment>

                <Switch>
                {/* <base href="/" />  */}
                        {localStorage.getItem('dtoken') || localStorage.getItem('htoken') || localStorage.getItem('ptoken') || localStorage.getItem('atoken') ? <Route exact path='/patient/login'  component={()=><Logout>patient</Logout>} /> :  <Route exact path='/patient/login' component={PatientLogin} />}
                        {localStorage.getItem('dtoken') || localStorage.getItem('htoken') || localStorage.getItem('ptoken') || localStorage.getItem('atoken') ? <Route exact path='/patient/signup' component={()=><Logout>patient</Logout>} /> :  <Route exact path='/patient/signup' component={PatientSignup} />}
                        
                        {localStorage.getItem('ptoken') || localStorage.getItem('dtoken') || localStorage.getItem('htoken') || localStorage.getItem('atoken') ? <Route exact path='/hospital/login' component={()=><Logout>hospital</Logout>}/> :  <Route exact path='/hospital/login'component={HospitalLogin}/>}
                        {localStorage.getItem('ptoken') || localStorage.getItem('dtoken') || localStorage.getItem('htoken') || localStorage.getItem('atoken') ? <Route exact path='/hospital/signup' component={()=><Logout>hospital</Logout>}/> :  <Route exact path='/hospital/signup'component={HospitalSignup}/>}
                        
                        {localStorage.getItem('ptoken') || localStorage.getItem('htoken') || localStorage.getItem('dtoken') || localStorage.getItem('atoken') ? <Route exact path='/doctor/login' component={()=><Logout>doctor</Logout>}/> : <Route exact path='/doctor/login' component={DoctorLogin}/> }
                        {localStorage.getItem('ptoken') || localStorage.getItem('dtoken') || localStorage.getItem('atoken') ? <Route exact path='/doctor/signup' component={()=><Logout>doctor</Logout>}/> : <Route exact path='/doctor/signup' component={DoctorSignup}/> }
                        
                        {localStorage.getItem('ptoken') || localStorage.getItem('dtoken') || localStorage.getItem('htoken') || localStorage.getItem('atoken')? <Route exact path='/admin/login' component={()=><Logout>admin</Logout>}/> : <Route exact path='/admin/login' component={AdminLogin}/>}
                        {localStorage.getItem('atoken') ? <Route exact path='/admin/analytics' component={Analytics} /> : null}

                        {localStorage.getItem('ptoken') ? <Route exact path='/patient/report' component={PatientReport} /> : null}
                        {localStorage.getItem('dtoken') ? <Route exact path='/doctor/interface' component={DoctorInterface} /> : null}
                        {localStorage.getItem('dtoken') ? <Route exact path='/doctor/report' component={DReport} /> : null}
                        {localStorage.getItem('htoken') ? <Route exact path='/hospital/report' component={HReport} /> : null}
                        {localStorage.getItem('dtoken') ? <Route exact path='/doctor/upload-report' component={UploadReport} /> : null}
                        {localStorage.getItem('dtoken') ? <Route exact path='/doctor/update-report' component={UpdateReport} /> : null}
         
                        {localStorage.getItem('htoken') ? <Route exact path='/hospital/interface' component={HospitalInterface} /> : null}
                        {localStorage.getItem('ptoken') ? <Route exact path='/patient/report/:id' component={FullReport} /> : null}
                        {localStorage.getItem('dtoken') ? <Route exact path='/doctor/report/:id' component={FullReport} /> : null}
                        {localStorage.getItem('htoken') ? <Route exact path='/hospital/report/:id' component={FullReport} /> : null}
                        


                        <Route exact path='/' component={Home}/>
                        
                        <Route component={Notfound} />

                        
                    </Switch>
                    
            </React.Fragment>

        )
    }
}

export default withRouter(Main);