import React from 'react';
import {withRouter} from 'react-router-dom';

import Navbar from '../navbar/navbar';
import styles from './logout.module.css'

const logout=(props)=>{
    // console.log(props.children);

    const logoutHandler=()=>{
        localStorage.clear();
        props.history.push(props.history.location.pathname);
        
    }

    return(
        <React.Fragment>
            <Navbar/>
            <div className={styles.model}>
                <h1>logout</h1>
                <p>You have to logout current account first <br></br>to login from {props.children} account !</p>
                <div className={styles.wrap}>
                <button className={styles.btn} onClick={props.history.goBack}>Cancel</button>
                <button className={styles.btn} onClick={logoutHandler}>Logout</button>
                </div>
               
            </div>
        </React.Fragment>
    )
}

export default withRouter(logout);